.card img {
    border: 2px solid #000;
    width: 150px;
height: 150px;
  }
  
  .card .front {
    transform: rotateY(90deg);
    position: absolute;
    transition: all ease-in 0.2s ;
  }

  .flipped .front {
    transform: rotateY(0deg);
    transition-delay: 0.2s;
  }


  .card .back {
    transition: all ease-in 0.2s ;
    transition-delay: 0.2s;
  }

  .flipped .back {
    transform: rotateY(90deg);
    transition-delay: 0.0s;
  }


  .card {
    position: relative;
  }